<template>
	<!--#@# 组件路径有点复杂唉，  -->
	<view class="r-top">
		<textarea :maxlength="len" placeholder-style="color:rgba(153,153,153,1);" v-model="content" :placeholder="pl" />
		<view class="r-top-t">
			<!--#@# flex 可以用flex-direction, space-between这种既调用的CSS又有HTML，会让人看不懂-->
		 	<view></view>
			<view> {{contentLength}}/{{len}}字</view>
			<!--#@# 直接content.length 试试？简单的JS属性命令是可以直接用的，不过COMPUTED会用也是挺好的事儿-->
		 </view>
	</view>
</template>

<script>
	export default {
		computed:{
			contentLength(){
				return this.content.length;
			}
		},
		props:{
			pl:{
				type: String,
				default: "请输入"
			},
			len:{
				type: Number,
				default: 200
			}
		},
		data() {
			return {
				content:''
			}
		},
	}
</script>

<style>
.r-top{
	padding: 10rpx;
	background: #FFFFFF;
	border-radius: 20rpx;
}
.r-top-t{
	display: flex;
	justify-content: space-between;
	font-family:PingFang SC;
	font-weight:400;
	line-height:20px;
	color:rgba(153,153,153,1);
	opacity:1;
}
</style>
